const _darwerToggle = (setState) => {
    setState(state => ({ ...state, 'showSidebar': !state.showSidebar }));
}

export default (props) => {

    let [state, setState] = useState({ 'showSidebar': false });
    useEffect(() => {
        let watchMedia = window.matchMedia('(max-width: 576px)');
        let listener = (res) => {
            setState(state => ({ ...state, 'showSidebar': res.matches }))
        }
        watchMedia.addListener(listener)

        // setState(state => ({ ...state, 'showSidebar': showSidebar }));
        return () => {
            watchMedia.removeListener(listener);
        }
    }, []);

    return <Layout style={{ height: '100vh' }}>
        {_renderHeader()}
        <Layout>
            {!state.showSidebar ? _renderSider() : null}
            {_renderBody(state, setState)}
        </Layout>
    </Layout>
}